export default {
    data(){
        return{
            djj:'',
        }
    },

    mounted(){
       if(this.$root.loginInfo.isLogin==true){
            this.init();
       }

    },
    methods:{
        starActivity(type) {
            if(this.$root.loginInfo.isLogin==true){
                if(type==1){
                    if(this.$root.phoneType=='ios'){
                        window.webkit.messageHandlers.mutual.postMessage({type: "7"});
                    }else{
                        android:AndroidFun.mutual('7');
                    }
                }else if(type==2){
                     this.$router.push('/myAppointment');
                }else if(type==3){
                        //判断是否实名认证 1、认证失败 2、认证通过 3、认证中 4、未认证 
                    if(this.$root.loginInfo.CSMZT==1){
                        this.$router.push('/AuditFailure');
                    }else if(this.$root.loginInfo.CSMZT==2){
                        //检查是否关联档案（接口）
                        this.selectInforMation();
                        //检查有几份档案（0 --提示无关联 1 关联档案（查询信息）--》 设置密码 （确定 提交密码+档案信息）  其他 提示管理员）
                    }else if(this.$root.loginInfo.CSMZT==3){
                        this.$router.push('/uploadDone');
                    }else if(this.$root.loginInfo.CSMZT==4){
                        this.$router.push('/RealNameSystem');
                    }
                }else if(type==4){
                    this.$router.push('/PatientManage');
                }else if(type==5){
                    this.$router.push('/visitCard');
                }
  
            }else{
                if(this.$root.phoneType=='ios'){
                    window.webkit.messageHandlers.mutual.postMessage({type: "1"});
                }else{
                    android:AndroidFun.mutual('1');
                }
            }
        
            
        },
         //检查是否关联档案（接口）
        selectInforMation(){
            this.$ajax.post('',{
                'MSH.1':'healthFileAssociateService',
                'MSH.2':'isAssociated',
                'CHZBM':this.$root.loginInfo.CHZBM,
                'token':this.$root.loginInfo.token,
            }).then(data => {
                if(typeof data !='undefined'){
                    if(data.status==true){
                        this.$router.push('/FileLogin');//输入密码登陆
                    }else{
                        this.$router.push({name: "AssociatedFiles"});     
                    }
                }
            }).catch(error => {
                console.log(error);
            });
        },
        init(){  // 初始化列表
            this.$ajax.post('',{
                "MSH.1": "myappointment",
                "MSH.2": "listAppointment",
                'token':this.$root.loginInfo.token, //token
                'IJZZT':1, //    预约状态
                'pageIndex':1,
                'pageSize':15,
            }).then(res => {
                this.$vux.loading.hide();
                if(res.rows && res.rows.length > 0){   
                    this.djj = '待就诊 '+res.rows[0].DJZSJ;                   
                }else{
                    if(this[pageIndex] > 1){
                        this.$vux.toast.text("没有更多了", "bottom");
                    }else{
                        this.noData = true;
                    }
                    
                    
                }

                
            }).catch(error => {
                console.log(error);
            })
        },

    }

}



